
.component--location-item {
  background: transparent;
  display: inline-block;
  float: left;
  font-size: 1.25rem;
  font-weight: 300;
  margin: 0 1px 1px 0;
  transition: box-shadow 0.3s ease, background 0.3s ease, -webkit-transform 0.3s ease;
  transition: transform 0.3s ease, box-shadow 0.3s ease, background 0.3s ease;
  transition: transform 0.3s ease, box-shadow 0.3s ease, background 0.3s ease, -webkit-transform 0.3s ease;
  box-shadow: 0 0 0px rgba(0,0,0,0);
  display: inline-block;
  width: 100%;
  position: relative;
  overflow: hidden;
  cursor: pointer;
}
.component--location-item a {
  color: #002835;
  display: block;
  padding: 0.625rem 1.25rem;
  line-height: 1.2;
}
.component--location-item:hover {
  background: rgba(233,30,99,0.7);
}
@media (min-width: 769px) {
.component--location-item:hover {
    -webkit-transform: scale(1.05);
        -ms-transform: scale(1.05);
            transform: scale(1.05);
    box-shadow: 0 0 10px rgba(0,0,0,0.15);
    z-index: 1;
    background: rgba(233,30,99,0.7);
}
.component--location-item:hover button {
    opacity: 1;
}
.component--location-item:active {
    -webkit-transform: scale(0.97);
        -ms-transform: scale(0.97);
            transform: scale(0.97);
}
}
.component--location-item button {
  position: absolute;
  right: -2.5rem;
  top: 0;
  bottom: 0;
  border: 0;
  background: #e91e63;
  color: #f6f9fa;
  height: 100%;
  width: 3.125rem;
  transition: right 0.3s ease, opacity 0.3s ease;
  opacity: 0;
  text-align: center;
}
.component--location-item button span {
  display: block;
}
.component--location-item button:hover {
  right: -0.125rem;
}
@media (max-width: 768px) {
.component--location-item {
    width: 48%;
    float: left;
}
.component--location-item button {
    right: -1.25rem;
    opacity: 1;
    border-radius: 50%;
    width: 2rem;
    height: 2rem;
    line-height: 1.75rem;
    -webkit-transform-origin: center center;
        -ms-transform-origin: center center;
            transform-origin: center center;
    -webkit-transform: translate(-50%, -50%) scale(0.7);
        -ms-transform: translate(-50%, -50%) scale(0.7);
            transform: translate(-50%, -50%) scale(0.7);
    top: 50%;
}
.component--location-item button:hover {
    right: -1.25rem;
}
}


