
.component--home {
  position: relative;
  background: #f6f9fa;
  box-shadow: 0px 0px 60px rgba(0,0,0,0.2);
  text-align: center;
  overflow: hidden;
}
.component--home .banner {
  height: 15.625rem;
  background-size: cover;
  background-position: center 100%;
  background-repeat: no-repeat;
  position: relative;
}
.component--home .copy,
.component--home .main-offices {
  max-width: 37.5rem;
  margin: 0 auto;
  padding-left: 1.875rem;
  padding-right: 1.875rem;
}
.component--home .main-offices {
  padding: 1.25rem;
  padding-bottom: 3.75rem;
}
.component--home .main-offices a.btn {
  margin: 0 0.625rem;
  min-width: 8.75rem;
}
@media (max-width: 768px) {
.component--home {
    overflow: visible;
}
.component--home .main-offices a.btn {
    display: block;
    margin: 0.5rem;
}
}


