
.component--weather-daylight {
  padding: 1.25rem 2.5rem;
  max-width: 350px;
  margin: 0 auto;
}
@media (max-width: 1024px) {
.component--weather-daylight {
    padding: 0.9375rem;
}
}
.component--weather-daylight .label {
  color: #002835;
  text-align: center;
  margin-top: 3.75rem;
}
.component--weather-daylight .weather-daylight--ruler {
  position: relative;
}
.component--weather-daylight .ruler {
  border-radius: 1.25rem;
  height: 1.25rem;
}
.component--weather-daylight .weather-daylight--night {
  width: 100%;
  background: #004a63;
}
.component--weather-daylight .weather-daylight--day {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255,235,59,0.95);
  width: initial;
}
.component--weather-daylight .marker {
  position: absolute;
  top: -1.875rem;
  color: #009688;
  font-size: 1.625rem;
}
.component--weather-daylight .time {
  font-size: 2.25rem;
  color: #002835;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}
.component--weather-daylight .sunset,
.component--weather-daylight .sunrise {
  top: 2.25rem;
}
.component--weather-daylight .sunrise {
  -webkit-transform: translateX(-40%);
      -ms-transform: translateX(-40%);
          transform: translateX(-40%);
}
.component--weather-daylight .sunset {
  -webkit-transform: translateX(40%);
      -ms-transform: translateX(40%);
          transform: translateX(40%);
}


