
.component--weather-condition {
  padding: 1.25rem 2.5rem;
  text-align: center;
  padding-top: 0;
}
@media (max-width: 1024px) {
.component--weather-condition {
    padding: 0.9375rem;
}
}
.component--weather-condition .label {
  color: #002835;
}
.component--weather-condition .wi {
  font-size: 12.5rem;
  line-height: 1;
  color: #009688;
}
.component--weather-condition .wi:before {
  vertical-align: text-bottom;
}
@media (max-width: 768px) {
.component--weather-condition .wi:before {
    vertical-align: initial;
}
}


