
.component--weather-stats {
  padding: 1.25rem 2.5rem;
}
@media (max-width: 1024px) {
.component--weather-stats {
    padding: 0.9375rem;
}
}
.component--weather-stats:after {
  content: "";
  display: table;
  clear: both;
}
.component--weather-stats .weather-stats--column {
  width: 33.333333333333336%;
  float: left;
  text-align: center;
}
.component--weather-stats p,
.component--weather-stats p .wi {
  color: #002835;
  font-size: 1.125rem;
}
.component--weather-stats .wi {
  color: #009688;
  font-size: 2.5rem;
  text-align: middle;
  margin-bottom: 1.25rem;
  -webkit-transform: translateX(-25%);
      -ms-transform: translateX(-25%);
          transform: translateX(-25%);
}


