
.component--weather-wind {
  padding: 1.25rem 2.5rem;
  text-align: center;
}
@media (max-width: 1024px) {
.component--weather-wind {
    padding: 0.9375rem;
}
}
.component--weather-wind:after {
  content: "";
  display: table;
  clear: both;
}
.component--weather-wind p {
  color: #002835;
}
.component--weather-wind .weather-wind--column {
  width: 33%;
  display: inline-block;
}
.component--weather-wind .wi {
  font-size: 3.75rem;
  color: #009688;
}
.component--weather-wind .weather-wind--vane {
  -webkit-transform-origin: center center;
      -ms-transform-origin: center center;
          transform-origin: center center;
  display: inline-block;
}
.component--weather-wind .weather-wind--vane .wi-wind {
  -webkit-animation-name: pulse;
          animation-name: pulse;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
}
@-webkit-keyframes pulse {
0%, 100% {
    -webkit-transform: rotate(-2deg);
            transform: rotate(-2deg);
}
50% {
    -webkit-transform: rotate(2deg);
            transform: rotate(2deg);
}
}
@keyframes pulse {
0%, 100% {
    -webkit-transform: rotate(-2deg);
            transform: rotate(-2deg);
}
50% {
    -webkit-transform: rotate(2deg);
            transform: rotate(2deg);
}
}


