
.component--navigation {
  width: 200px;
  float: left;
  margin-top: 1.25rem;
  background: linear-gradient(180deg, #00bcd4 -100%, #009688);
  transition: none;
}
.component--navigation .component--navigation--toggle {
  display: none;
}
.component--navigation h5 {
  margin: 0;
  color: #f6f9fa;
}
.component--navigation .controller--user-locations {
  padding-bottom: 1rem;
}
.component--navigation .controller--user-locations:after {
  content: "";
  display: table;
  clear: both;
}
.component--navigation .controller--user-locations h5 {
  padding: 1rem;
}
.component--navigation .controller--user-locations .component--location-item:hover {
  background: linear-gradient(45deg, #8ac249, #009688);
}
.component--navigation .controller--user-locations .component--location-item a {
  color: #f6f9fa;
}
@media (max-width: 768px) {
.component--navigation {
    width: 100%;
    float: none;
    position: absolute;
    z-index: 4;
    left: 0;
    margin-top: 0;
    transition: -webkit-transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
    -webkit-transform: translateX(-100%);
        -ms-transform: translateX(-100%);
            transform: translateX(-100%);
}
.component--navigation.open {
    -webkit-transform: none;
        -ms-transform: none;
            transform: none;
}
}


