
.component--controller {
  max-width: 1200px;
  margin: 0 auto;
}
.component--controller nav {
  float: left;
}
.component--controller nav img {
  width: 90px;
}
.component--controller nav a,
.component--controller nav img {
  line-height: 1.8;
  vertical-align: middle;
}
.component--controller nav a.router-link-active {
  font-size: 1.75rem;
  font-weight: 100;
  margin-left: 1.25rem;
}
.component--controller header {
  text-align: right;
  padding-bottom: 0.625rem;
}
.component--controller header:after {
  content: "";
  display: table;
  clear: both;
}
.component--controller .content-pane {
  margin-left: 200px;
}
.component--controller .component--clock {
  display: inline-block;
  line-height: 50px;
  vertical-align: top;
  margin-right: 1rem;
}
.component--controller .component--unit-toggle {
  display: inline-block;
}
@media (max-width: 768px) {
.component--controller nav img {
    width: 70px;
}
.component--controller .content-pane {
    margin-left: 0;
}
}
@media (max-width: 580px) {
.component--controller .component--clock {
    display: none;
}
}


