
.component--weather-temperature {
  padding: 1.25rem 2.5rem;
}
@media (max-width: 1024px) {
.component--weather-temperature {
    padding: 0.9375rem;
}
}
@media (min-width: 769px) {
.component--weather-temperature {
    margin: 1.875rem 0;
}
}
.component--weather-temperature .weather-temperature--gauge {
  border-radius: 20px;
  height: 1.25rem;
  background: linear-gradient(90deg, #00bcd4, #e91e63);
  width: calc(100% - 140px);
  margin: 0 auto;
  position: relative;
}
.component--weather-temperature .weather-temperature--label-low,
.component--weather-temperature .weather-temperature--label-high {
  position: absolute;
  text-align: center;
}
.component--weather-temperature .weather-temperature--label-low p,
.component--weather-temperature .weather-temperature--label-high p {
  color: #002835;
  font-size: 1.25rem;
  line-height: 1.25rem;
}
.component--weather-temperature .weather-temperature--label-low {
  left: -4.375rem;
}
.component--weather-temperature .weather-temperature--label-high {
  right: -4.375rem;
}


