
.component--weather-detail {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: #fafafa;
  text-align: left;
}
@media (max-width: 768px) {
.component--weather-detail {
    height: auto;
    bottom: initial;
    min-height: 100%;
}
}
@media (max-width: 580px) {
.component--weather-detail {
    padding-bottom: 2.5rem;
}
}
.component--weather-detail .title {
  text-align: center;
}
.component--weather-detail .weather-detail--controls {
  background: rgba(90,90,91,0.1);
  overflow: hidden;
  position: relative;
}
.component--weather-detail .weather-detail--content {
  padding: 0rem 2.5rem;
}
.component--weather-detail .weather-detail--content:after {
  content: "";
  display: table;
  clear: both;
}
.component--weather-detail .weather-detail--column {
  width: 50%;
  display: inline-block;
  vertical-align: middle;
  max-width: 27.5rem;
}
@media (max-width: 580px) {
.component--weather-detail .weather-detail--column {
    width: auto;
    display: block;
    margin: 0 auto;
}
}


