
.component--navigation-toggle {
  display: inline-block;
  width: 3.125rem;
  margin-left: 0.625rem;
  vertical-align: super;
}
@media (min-width: 769px) {
.component--navigation-toggle {
    display: none;
}
}
.component--navigation-toggle .bar {
  height: 3px;
  background: #f6f9fa;
  -webkit-transform-origin: center center;
      -ms-transform-origin: center center;
          transform-origin: center center;
  transition: color 0.3s ease, opacity 0.1s ease, -webkit-transform 0.3s ease;
  transition: transform 0.3s ease, color 0.3s ease, opacity 0.1s ease;
  transition: transform 0.3s ease, color 0.3s ease, opacity 0.1s ease, -webkit-transform 0.3s ease;
}
.component--navigation-toggle .bar:not(:last-of-type) {
  margin-bottom: 0.75rem;
}
.component--navigation-toggle.open .bar:first-of-type,
.component--navigation-toggle.open .bar:last-of-type {
  background: #e91e63;
}
.component--navigation-toggle.open .bar:first-of-type {
  -webkit-transform: translateY(1rem) rotate(45deg);
      -ms-transform: translateY(1rem) rotate(45deg);
          transform: translateY(1rem) rotate(45deg);
}
.component--navigation-toggle.open .bar:last-of-type {
  -webkit-transform: translateY(-1rem) rotate(-45deg);
      -ms-transform: translateY(-1rem) rotate(-45deg);
          transform: translateY(-1rem) rotate(-45deg);
}
.component--navigation-toggle.open .bar:nth-of-type(2) {
  opacity: 0;
}


