
.component--unit-toggle {
  width: 6.25rem;
  background: rgba(246,249,250,0.3);
  position: relative;
  cursor: pointer;
  transition: -webkit-transform 200ms ease;
  transition: transform 200ms ease;
  transition: transform 200ms ease, -webkit-transform 200ms ease;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.component--unit-toggle:after {
  content: "";
  display: table;
  clear: both;
}
.component--unit-toggle:hover {
  -webkit-transform: scale(1.02);
      -ms-transform: scale(1.02);
          transform: scale(1.02);
}
.component--unit-toggle:active {
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
}
.component--unit-toggle .unit {
  width: 50%;
  height: 3rem;
  float: left;
  transition: color 200ms ease;
  font-size: 1.625rem;
  font-weight: 300;
  text-align: center;
  line-height: 3.0625rem;
  padding-right: 2px;
  color: rgba(246,249,250,0.7);
}
.component--unit-toggle:before {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: #f6f9fa;
  width: 50%;
  z-index: -1;
  transition: left 200ms ease;
}
.component--unit-toggle.metric .unit:first-of-type {
  color: #e91e63;
}
.component--unit-toggle.imperial .unit:last-of-type {
  color: #e91e63;
}
.component--unit-toggle.imperial:before {
  left: 50%;
}


