
.component--office-menu {
  position: relative;
}
.component--office-menu .label {
  padding: 1.625rem 1rem;
  background: linear-gradient(160deg, #00bcd4 -200%, #002835);
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
  cursor: pointer;
}
.component--office-menu .component--location-item a {
  color: #f6f9fa;
}
.component--office-menu .location-menu {
  padding: 1.25rem;
  background: linear-gradient(160deg, #00bcd4 -500%, #002835);
  box-shadow: 0 0 640px rgba(0,0,0,0.5);
}
.component--office-menu .location-menu:after {
  content: "";
  display: table;
  clear: both;
}
@media (min-width: 769px) {
.component--office-menu .component--location-item {
    width: 33%;
}
.component--office-menu .location-menu {
    position: absolute;
    width: 50rem;
    left: 100%;
    top: -1.25rem;
    z-index: 2;
}
.component--office-menu .location-menu:before {
    content: '';
    display: block;
    position: absolute;
    left: -200px;
    top: 0;
    height: 120px;
    width: 200px;
    cursor: pointer;
}
.component--office-menu.active .label {
    -webkit-transform: scale(1.02);
        -ms-transform: scale(1.02);
            transform: scale(1.02);
}
}
@media (max-width: 768px) {
.component--office-menu .location-menu {
    padding: 1.25rem 0;
}
}
@media (min-width: 769px) and (max-width: 1024px) {
.component--office-menu .location-menu {
    width: 40.625rem;
}
}


